$bgcolor: #2020208f;
$fgcolor: #e2e2e2;

.PowerMenu {
  background-color: $bgcolor;
  color: $fgcolor;
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 10px;
  padding: 15px 25px;
  display: flex;
  opacity: 0;
  pointer-events: none;
  transition: ease 0.1s;

  * {
    margin: 0;
  }

  &.active {
    opacity: 1;
    pointer-events: all;
  }

  .PowerMenuInteraction {
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 20px;
    border-radius: 10px;
    margin-right: 10px;

    &:last-child {
      margin: 0;
    }

    i {
      font-size: 30px;
    }

    &:hover {
      background-color: #9d9d9d11;
    }

    .PowerMenuInteractionTitle {
      margin-top: 15px;
      text-align: center;
      font-size: 14px;
    }
  }

  &.whitesur {
    background-color: #151515;
    border: 0.5px solid lighten(#202023, 8%);
  }
}
